"use client";

import React from "react";
import { AlertTriangle, ArrowRight, Lock, Zap } from "lucide-react";
import type { AIState } from "./SalesAgentUI";

export default function SystemStateBar({ state }: { state: AIState }) {
  return (
    <div className="flex h-12 w-full shrink-0 items-center gap-6 border-b border-zinc-800 bg-zinc-900/40 px-6 backdrop-blur-md">
      <div className="flex items-center gap-3">
        <div className="flex items-center gap-1.5 text-[11px] font-bold uppercase tracking-wider text-zinc-500">
          <Zap size={12} />
          <span>Active Objective</span>
        </div>
        <div className="flex items-center gap-2 rounded border border-zinc-800 bg-zinc-950/50 px-2 py-1">
          <span className="text-xs font-medium text-zinc-200">Close Acme Corp (Enterprise)</span>
          <span className="h-3 w-px bg-zinc-800" />
          <span className="text-[10px] text-zinc-500">Q4 Pipeline</span>
        </div>
      </div>

      <div className="h-4 w-px bg-zinc-800" />

      <div className="flex items-center gap-3">
        <span className="text-[11px] font-bold uppercase tracking-wider text-zinc-500">Phase</span>
        <div className="flex items-center gap-1">
          <span className={`text-xs font-semibold ${state.phase === "Negotiation" ? "text-indigo-400" : "text-zinc-400"}`}>
            {state.phase}
          </span>
          <ArrowRight size={10} className="text-zinc-600" />
          <span className="text-xs text-zinc-600">Closing</span>
        </div>
      </div>

      <div className="h-4 w-px bg-zinc-800" />

      {state.blocker && (
        <div className="flex items-center gap-2 rounded bg-rose-950/20 px-2 py-1 ring-1 ring-rose-900/30">
          <Lock size={10} className="text-rose-400" />
          <span className="text-[11px] font-bold uppercase tracking-wider text-rose-400">Blocked By</span>
          <span className="text-xs font-medium text-rose-200">{state.blocker}</span>
        </div>
      )}

      <div className="ml-auto flex items-center gap-2">
        {state.health === "at_risk" && (
          <div className="flex items-center gap-1.5 text-amber-500">
            <AlertTriangle size={12} />
            <span className="text-[10px] font-mono uppercase">Anomaly Detected</span>
          </div>
        )}
        {state.health === "critical" && (
          <div className="flex items-center gap-1.5 text-rose-500">
            <AlertTriangle size={12} />
            <span className="text-[10px] font-mono uppercase">Critical Risk</span>
          </div>
        )}
      </div>
    </div>
  );
}
