"use client";

import React, { useEffect, useRef, useState } from "react";
import { Activity, Bell, Bot, Radio, Settings } from "lucide-react";
import ModeSelector, { Mode } from "./ModeSelector";
import SystemStateBar from "./SystemStateBar";
import ActionQueue, { QueueItem } from "./ActionQueue";
import SystemEvent, { EventItem } from "./SystemEvent";
import CommandInput from "./CommandInput";
import IntelligencePanel from "./IntelligencePanel";

export type AIState = {
  phase: "Prospecting" | "Validation" | "Negotiation" | "Closing";
  status: "active" | "paused" | "awaiting_human";
  blocker: string | null;
  probability: number;
  health: "healthy" | "at_risk" | "critical";
};

const INITIAL_STATE: AIState = {
  phase: "Negotiation",
  status: "awaiting_human",
  blocker: "Legal Review (MSA)",
  probability: 68,
  health: "at_risk",
};

const INITIAL_EVENTS: EventItem[] = [
  {
    id: "e1",
    timestamp: "08:00:21",
    type: "log",
    source: "CRM Connector",
    content: 'Synced 14 emails from thread "Re: MSA Redlines".',
  },
  {
    id: "e2",
    timestamp: "08:00:25",
    type: "analysis",
    source: "Sentiment Engine",
    content:
      'Detected negative sentiment in latest reply from Sarah (CISO). Concern topic: "Data Residency".',
    metadata: { confidence: 98, impact: "high" },
  },
  {
    id: "e3",
    timestamp: "08:01:00",
    type: "alert",
    source: "Risk Monitor",
    content:
      "PROBABILITY DROP: Deal score decreased by 12% due to CISO objection.",
  },
  {
    id: "e4",
    timestamp: "08:01:05",
    type: "decision",
    source: "Strategy Core",
    content:
      'Generated counter-strategy: "Compliance Override Packet". Awaiting supervisor approval to execute.',
    actions: [
      { id: "a1", label: "Approve & Execute Strategy", style: "primary" },
      { id: "a2", label: "Edit Packet", style: "secondary" },
    ],
  },
];

const INITIAL_QUEUE: QueueItem[] = [
  {
    id: "q1",
    label: "Analyze Thread Sentiment",
    status: "completed",
    timestamp: "08:00 AM",
  },
  {
    id: "q2",
    label: "Update CRM Probability",
    status: "completed",
    timestamp: "08:01 AM",
  },
  {
    id: "q3",
    label: "Generate Compliance Docs",
    status: "running",
    timestamp: "Now",
  },
  { id: "q4", label: "Email Draft: CISO", status: "pending", timestamp: "Queued" },
];

export default function SalesAgentUI() {
  const [aiState, setAiState] = useState<AIState>(INITIAL_STATE);
  const [events, setEvents] = useState<EventItem[]>(INITIAL_EVENTS);
  const [queue, setQueue] = useState<QueueItem[]>(INITIAL_QUEUE);
  const [activeMode, setActiveMode] = useState<Mode>("live");
  const [showPanel, setShowPanel] = useState(true);

  const scrollRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
  }, [events]);

  const handleCommand = (cmd: string) => {
    const now = new Date().toLocaleTimeString("en-US", {
      hour12: false,
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
    });

    const cmdEvent: EventItem = {
      id: `cmd-${Date.now()}`,
      timestamp: now,
      type: "command",
      source: "SUPERVISOR",
      content: cmd,
    };
    setEvents((prev) => [...prev, cmdEvent]);

    setQueue((prev) => [
      ...prev,
      { id: `q-${Date.now()}`, label: `Process command: ${cmd}`, status: "running", timestamp: "Now" },
    ]);

    setTimeout(() => {
      const responseEvent: EventItem = {
        id: `sys-${Date.now()}`,
        timestamp: new Date().toLocaleTimeString("en-US", {
          hour12: false,
          hour: "2-digit",
          minute: "2-digit",
          second: "2-digit",
        }),
        type: "log",
        source: "System Core",
        content: "Command acknowledged. Overriding autonomous sequence.",
      };

      setEvents((prev) => [...prev, responseEvent]);
      setAiState((prev) => ({ ...prev, status: "active", blocker: null }));

      setQueue((prev) =>
        prev.map((q) =>
          q.label.startsWith("Process command:") && q.status === "running"
            ? { ...q, status: "completed", timestamp: "Done" }
            : q
        )
      );
    }, 800);
  };

  return (
    <div className="flex h-full w-full flex-col bg-zinc-950 text-sm">
      <header className="flex h-12 shrink-0 items-center justify-between border-b border-zinc-800 bg-zinc-950 px-4 z-50">
        <div className="flex items-center gap-4">
          <div className="flex items-center gap-2">
            <div className="flex h-6 w-6 items-center justify-center rounded bg-indigo-500/20 text-indigo-400 ring-1 ring-inset ring-indigo-500/30">
              <Bot size={14} />
            </div>
            <span className="font-semibold tracking-tight text-zinc-100">
              SalesOS <span className="opacity-50">Enterprise</span>
            </span>
          </div>
          <div className="h-4 w-px bg-zinc-800" />
          <div className="flex items-center gap-2">
            <Radio size={12} className="text-emerald-500 animate-pulse" />
            <span className="text-[11px] font-medium uppercase tracking-wider text-zinc-400">
              Autonomous Mode Active
            </span>
          </div>
        </div>

        <ModeSelector current={activeMode} onChange={setActiveMode} />

        <div className="flex items-center gap-3">
          <div className="flex items-center gap-1.5 rounded-full bg-zinc-900 px-2.5 py-1 ring-1 ring-zinc-800">
            <Activity
              size={12}
              className={aiState.health === "critical" ? "text-rose-500" : aiState.health === "at_risk" ? "text-amber-500" : "text-emerald-500"}
            />
            <span className="text-[10px] font-medium text-zinc-400">System Health</span>
          </div>
          <div className="h-4 w-px bg-zinc-800" />
          <button className="text-zinc-500 hover:text-zinc-200">
            <Bell size={16} />
          </button>
          <button
            onClick={() => setShowPanel((v) => !v)}
            className="text-zinc-500 hover:text-zinc-200"
          >
            <Settings size={16} />
          </button>
        </div>
      </header>

      <SystemStateBar state={aiState} />

      <div className="flex flex-1 overflow-hidden">
        <div className="relative flex flex-1 flex-col min-w-0">
          <div className="absolute right-6 top-6 z-10">
            <ActionQueue queue={queue} />
          </div>

          <div
            ref={scrollRef}
            className="flex-1 overflow-y-auto px-6 py-6 scrollbar-thin scrollbar-track-transparent scrollbar-thumb-zinc-800"
          >
            <div className="mx-auto max-w-4xl flex flex-col gap-1 pb-10">
              <div className="flex items-center gap-4 py-4 opacity-50">
                <div className="h-px flex-1 bg-zinc-800" />
                <span className="text-[10px] font-mono text-zinc-500">
                  SESSION START: {new Date().toLocaleDateString()}
                </span>
                <div className="h-px flex-1 bg-zinc-800" />
              </div>

              {events.map((event) => (
                <SystemEvent key={event.id} event={event} />
              ))}
            </div>
          </div>

          <div className="shrink-0 z-20 bg-zinc-950 border-t border-zinc-800">
            <CommandInput onExecute={handleCommand} />
          </div>
        </div>

        {showPanel && <IntelligencePanel state={aiState} />}
      </div>
    </div>
  );
}
